.pictures {
  margin-top: 130px;
  &__title {
    text-align: center; }
  &__gallery {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between; }
  &__preview {
    position: relative;
    width: 370px;
    height: 370px;
    overflow: hidden;
    font-size: 0;
    margin-bottom: 30px;
    cursor: pointer; }
  &__picture {
    font-size: 0;
    min-width: 100%;
    min-height: 100%;
    transition: transform 2s; }
  &__preview:before {
    color: #ffc06f;
    opacity: 0;
    z-index: 100;
    font: normal normal normal 14px/1 FontAwesome;
    content: '\f00e';
    width: 50px;
    height: 50px;
    position: absolute;
    left: calc(50% - 25px);
    top: calc(50% - 25px);
    font-size: 50px; }
  &__preview:hover {
    .pictures__picture {
      transform: scale(1.2); }
    &:before {
      opacity: 1; } } }
.mfp-arrow {
  display: none !important; }
